@charset "UTF-8";
/*   подключение шрифтов   */
/* Bebas Neue Regular*/
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Bebas Neue";
  font-display: swap;
  src: url("../fonts/BebasNeue-400.woff2") format("woff2"), url("../fonts/BebasNeue-400.woff") format("woff"), url("../fonts/BebasNeue-400.ttf") format("truetype"); }

/* Bebas Neue Bold*/
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Bebas Neue";
  font-display: swap;
  src: url("../fonts/BebasNeue-700.woff2") format("woff2"), url("../fonts/BebasNeue-700.woff") format("woff"), url("../fonts/BebasNeue-700.ttf") format("truetype"); }

/* Roboto Condensed Light */
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-300.woff2") format("woff2"), url("../fonts/RobotoCondensed-300.woff") format("woff"), url("../fonts/RobotoCondensed-300.ttf") format("truetype"); }

/* Roboto Condensed Regular */
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-400.woff2") format("woff2"), url("../fonts/RobotoCondensed-400.woff") format("woff"), url("../fonts/RobotoCondensed-400.ttf") format("truetype"); }

/* Roboto Condensed Bold */
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-700.woff2") format("woff2"), url("../fonts/RobotRobotoCondensedcondensed-700.woff") format("woff"), url("../fonts/RobotoCondensed-700.ttf") format("truetype"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

html {
  scroll-behavior: smooth; }

body {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654; }

.page-header {
  background: url("../img/ameer-basheer_desktop@1x.jpg") no-repeat 41vw/500px 556px, url("../img/bg-gradient_desk@1x.jpg") no-repeat 100% 100%/cover; }
  @media (min-resolution: 1.5dppx) {
    .page-header {
      background: url("../img/ameer-basheer_desktop@2x.jpg") no-repeat 41vw/500px 556px, url("../img/bg-gradient_desk@2x.jpg") no-repeat 100% 100%/cover; } }

.page-header__inner {
  position: relative;
  max-width: 1366px;
  margin: auto; }

.page-header__head {
  padding: 30px 103px; }

.page-header__main {
  width: 350px;
  padding-top: 82px;
  padding-bottom: 93px;
  padding-left: 148px; }

/*   tablet version   */
@media (max-width: 1365px) {
  .page-header {
    background: url("../img/ameer-basheer_tablet@1x.jpg") no-repeat 52vw/324px 390px, url("../img/bg-gradient_tablet@1x.jpg") no-repeat 100% 100%/cover; } }
  @media (max-width: 1365px) and (min-resolution: 1.5dppx) {
    .page-header {
      background: url("../img/ameer-basheer_tablet@2x.jpg") no-repeat 52vw/324px 390px, url("../img/bg-gradient_tablet@2x.jpg") no-repeat 100% 100%/cover; } }

@media (max-width: 1365px) {
  .page-header__head {
    padding: 40px 45px; }
  .page-header__main {
    width: 310px;
    padding-top: 31px;
    padding-bottom: 105px;
    padding-left: calc((100vw - 768px) / 2 + 59px); } }

/*   mobile version   */
@media (max-width: 767px) {
  .page-header {
    background: url("../img/bg-gradient_mobile@1x.jpg") no-repeat 100% 100%/cover; } }
  @media (max-width: 767px) and (min-resolution: 1.5dppx) {
    .page-header {
      background: url("../img/bg-gradient_mobile@2x.jpg") no-repeat 100% 100%/cover; } }

@media (max-width: 767px) {
  .page-header__head {
    padding: 20px 15px; }
  .page-header__main {
    width: 290px;
    margin: auto;
    padding-top: 52px;
    padding-bottom: 78px;
    padding-left: 0; } }

.head-header {
  display: flex;
  justify-content: space-between; }

.head-header__link-site,
.head-header__link-tel {
  font: 700 26px/26px "Bebas Neue", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.02em;
  transition: 0.3s; }
  .head-header__link-site:hover,
  .head-header__link-tel:hover {
    color: #003d85; }
  .head-header__link-site:active,
  .head-header__link-tel:active {
    color: #000000; }
  .head-header__link-site:focus,
  .head-header__link-tel:focus {
    outline: 3px solid #003d85; }

/*   tablet version   */
@media (max-width: 1365px) {
  .head-header__link-site,
  .head-header__link-tel {
    font-size: 24px;
    line-height: 24px; } }

/*   mobile version   */
@media (max-width: 767px) {
  .head-header__link-site,
  .head-header__link-tel {
    font-size: 20px;
    line-height: 20px; } }

.head-main {
  position: relative;
  display: flex;
  flex-direction: column;
  text-align: right; }

.head-main__city {
  font: 49px/49px "Bebas Neue", "Arial", sans-serif;
  color: #ffffff;
  letter-spacing: 0.03em; }

.head-main__title {
  margin-top: 0;
  margin-bottom: 0;
  text-transform: uppercase;
  margin-top: 10px;
  font: 700 109px/109px "Bebas Neue", "Arial", sans-serif;
  color: #ffffff;
  letter-spacing: 0.03em; }

.head-main__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  position: relative;
  top: -88px;
  left: -28px;
  width: 115px; }

.head-main__item {
  font: 14px/21px "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff; }

.head-main__abonements {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 224px;
  min-height: 54px;
  text-transform: uppercase;
  text-decoration: none;
  border: 4px solid #ffffff;
  margin-top: -5px;
  margin-left: auto;
  font: 12px/14px "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  letter-spacing: 0.05em;
  transition: 0.3s; }
  .head-main__abonements:hover {
    background: linear-gradient(111.72deg, #b80027 17.6%, #003d85 95.31%); }
  .head-main__abonements:active {
    background: linear-gradient(111.72deg, #003d85 17.6%, #b80027 95.31%); }
  .head-main__abonements:focus {
    outline: 5px solid #b80027; }

/*   tablet version   */
@media (max-width: 1365px) {
  .head-main__city {
    font-size: 34px;
    line-height: 34px; }
  .head-main__title {
    margin-top: 5px;
    font-size: 86px;
    line-height: 86px; }
  .head-main__list {
    top: -78px;
    left: -16px; }
  .head-main__abonements {
    width: 178px;
    margin-top: -22px; } }

/*   mobile version   */
@media (max-width: 767px) {
  .head-main__city {
    font-size: 28px;
    line-height: 28px; }
  .head-main__title {
    margin-top: 8px;
    font-size: 78px;
    line-height: 78px; }
  .head-main__list {
    top: -70px;
    left: -2px; }
  .head-main__abonements {
    width: 282px;
    height: 55px;
    margin-top: -17px; } }

.head-social__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  position: absolute;
  top: 482px;
  right: 102px; }

.head-social__item {
  margin-bottom: 14px;
  text-align: center; }
  .head-social__item a {
    font: 12px/14px "Roboto Condensed", "Arial", sans-serif;
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    transition: 0.3s; }
    .head-social__item a:hover {
      color: #003d85; }
    .head-social__item a:active {
      color: #000000; }
    .head-social__item a:focus {
      outline: 3px solid #003d85; }

/*   tablet version   */
@media (max-width: 1365px) {
  .head-social {
    display: none; } }

.page-main {
  background-color: #ffffff; }

.gym__inner {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1366px;
  margin: auto;
  padding: 120px 103px 120px 163px; }

.gym__article {
  width: 355px;
  margin-top: 46px;
  margin-right: auto; }

.gym__title {
  margin-top: 0;
  margin-bottom: 0;
  text-transform: uppercase;
  margin-bottom: 6px;
  font: 700 49px/49px "Bebas Neue", "Arial", sans-serif;
  letter-spacing: 0.03em; }

.gym__note {
  font-size: 18px;
  line-height: 27px; }

.gym__description {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 10px;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px; }
  .gym__description:nth-of-type(1) {
    margin-top: 40px; }

.gym__photo {
  width: 660px;
  height: 401px; }

.gym__video {
  position: relative;
  width: 364px;
  height: 228px;
  margin-top: -113px;
  margin-left: 18px; }
  .gym__video::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(20, 54, 122, 0.5); }
  .gym__video::after {
    content: "";
    position: absolute;
    top: 64px;
    left: 132px;
    width: 100px;
    height: 100px;
    background: url("../img/svg/play-video.svg") no-repeat center/contain;
    transition: 0.3s; }
  .gym__video:hover::after {
    transform: scale(1.2); }
  .gym__video:active::after {
    transform: scale(0.8); }
  .gym__video:focus::after {
    outline: 5px solid #003d85; }

/*   tablet version   */
@media (max-width: 1365px) {
  .gym__inner {
    flex-direction: column;
    align-items: center;
    padding: 100px 45px; }
  .gym__article {
    width: 80%;
    max-width: 678px;
    margin-top: 0;
    margin-right: 0;
    margin-bottom: 33px;
    margin-left: 0;
    text-align: center; }
  .gym__title {
    font-size: 42px;
    line-height: 42px; }
  .gym__description:nth-of-type(1) {
    margin-top: 36px; }
  .gym__description:nth-of-type(2n) {
    display: none; }
  .gym__photo {
    width: 678px;
    height: 345px; }
  .gym__video {
    width: 266px;
    height: 167px;
    margin-top: -84px;
    margin-left: 0; }
    .gym__video::after {
      top: 47px;
      left: 95px;
      width: 73px;
      height: 73px; } }

/*   mobile version   */
@media (max-width: 767px) {
  .gym__inner {
    padding: 70px 15px; }
  .gym__article {
    min-width: 290px;
    margin-bottom: 22px;
    text-align: left; }
  .gym__title {
    font-size: 32px;
    line-height: 32px; }
  .gym__description {
    width: 90%; }
  .gym__description:nth-of-type(1) {
    margin-top: 24px; }
  .gym__photo {
    width: 320px;
    height: 313px;
    margin-bottom: 40px;
    margin-left: -15px; }
  .gym__video {
    width: 320px;
    margin-top: 0;
    margin-left: -15px; }
    .gym__video::after {
      left: 125px; } }
